body {
  margin: 0;
  font-family: Montserrat, sans-serif;
}

.container {
  max-width: 1244px;
  margin: 0 auto;
  padding: 0 15px;
}

.social-icons {
  width: 115px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (min-width: 1200px) {
  .social-icons {
    display: flex;
  }
}
.social-icons__link {
  display: flex;
}
.social-icons__link:last-child {
  margin-right: 0;
}
.social-icons__link--mr {
  margin-right: 43px;
}
.social-icons--pd-top {
  width: 200px;
}
@media (min-width: 1200px) {
  .social-icons--pd-top {
    padding-top: 30px;
  }
}
.social-icons--mr60 {
  margin-right: 40px;
}
.social-icons--width {
  width: auto;
}

.nav {
  display: none;
}
@media (min-width: 1920px) {
  .nav {
    display: block;
    width: 665px;
  }
}
.nav__list {
  display: flex;
  padding: 0;
  list-style-type: none;
}
.nav__item {
  margin-right: 36px;
  font-size: 16px;
  font-weight: 700;
  line-height: 23px;
  letter-spacing: 0em;
  text-align: left;
  text-transform: uppercase;
}
.nav__item:last-child {
  margin-right: 0;
}
.nav__link {
  text-decoration: none;
  color: #4c858b;
}
.nav__link:after {
  background-color: #4c858b;
  display: block;
  content: "";
  height: 2px;
  width: 0%;
  transition: width 0.3s ease-in-out;
}
.nav__link:hover:after {
  width: 100%;
}

.group {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 4px;
  margin-right: 23px;
}
@media (min-width: 1920px) {
  .group {
    margin-right: 0;
  }
}
.group__icon {
  display: flex;
  justify-content: center;
  padding: 15px;
  border: 1px solid #e5e5e5;
  border-radius: 50%;
  cursor: pointer;
  margin-right: 16px;
  position: relative;
}
@media (min-width: 768px) {
  .group__icon {
    margin-right: 27px;
  }
}
.group__icon:last-child {
  margin-right: 0;
  padding: 12px;
}
@media (min-width: 768px) {
  .group__icon:last-child {
    padding: 15px;
    margin-right: 0;
  }
}
.group__icon--label:after {
  display: block;
  position: absolute;
  content: "10";
  font-family: Lato, sans-serif;
  color: #fff;
  font-weight: 800;
  font-size: 11px;
  display: flex;
  line-height: 13px;
  border-radius: 50%;
  width: 14px;
  height: 14px;
  padding: 3px;
  top: -2px;
  left: 31px;
  background: #e33e54;
}
@media (min-width: 768px) {
  .group__icon--label:after {
    top: -4px;
    right: 0;
  }
}
.group__icon:nth-child(-n+2) {
  display: none;
}
@media (min-width: 768px) {
  .group__icon:nth-child(-n+2) {
    display: flex;
  }
}
.group__icon:hover {
  box-shadow: 8px 6px 20px -6px rgba(0, 0, 255, 0.2);
  transition: 0.3s;
}

