.popup {
  position: relative;
  font-weight: 700;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  text-transform: uppercase;
  color: #ae2236;
  cursor: pointer;
  margin-right: 64px;
  transition: all 0.3s ease-in-out;
  display: none;
}
@media (min-width: 1920px) {
