    display: block;
  }
}

.popup:hover {
  color: #e42844;
  transition: all 0.3s ease-in-out;
}

.popup:after {
  display: block;
  content: "";
  position: absolute;
  width: 100%;
  padding-top: 5px;
  border-bottom: 1px dashed #ae2236;
  transition: all 0.3s ease-in-out;
}

.popup:hover:after {
  border-bottom: 1px dashed #e42844;
  transition: all 0.3s ease-in-out;
}

