.gallery {
  padding-top: 30px;
  padding-bottom: 46px;
}
@media (min-width: 768px) {
  .gallery {
    padding-top: 19px;
    padding-bottom: 58px;
  }
}
@media (min-width: 1200px) {
  .gallery {
    padding-top: 68px;
    padding-bottom: 136px;
  }
}
.gallery__container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
@media (min-width: 430px) {
  .gallery__container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 768px) {
  .gallery__container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 30px;
  }
}
@media (min-width: 1300px) {
  .gallery__container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 30px;
  }
}
.gallery__img {
  position: relative;
  filter: drop-shadow(0px 4px 10px rgba(0, 0, 0, 0.15));
  cursor: pointer;
  width: 130px;
  height: 120px;
  margin-bottom: 15px;
}
@media (min-width: 768px) {
  .gallery__img {
    width: 226px;
    height: 208px;
    margin-bottom: 0px;
  }
}
@media (min-width: 1920px) {
  .gallery__img {
    width: 282px;
    height: 260px;
  }
}
.gallery__img img {
  width: 130px;
  height: 120px;
}
@media (min-width: 768px) {
  .gallery__img img {
    width: 226px;
    height: 208px;
  }
}
@media (min-width: 1920px) {
  .gallery__img img {
    width: 282px;
    height: 260px;
  }
}
.gallery__img a {
  display: inline-block;
}
.gallery__img:after {
  display: block;
  content: "";
  position: absolute;
  border-left: 4px solid #4c858b;
  border-bottom: 4px solid #4c858b;
  width: 30px;
  height: 30px;
  bottom: 0px;
  transition: all 0.3s ease-in-out;
}
@media (min-width: 768px) {
  .gallery__img:after {
    width: 50px;
    height: 50px;
    bottom: 0px;
  }
}
.gallery__img:before {
  display: block;
  content: "";
  position: absolute;
  border-right: 4px solid #4c858b;
  border-top: 4px solid #4c858b;
  width: 30px;
  height: 30px;
  right: 0;
  transition: all 0.3s ease-in-out;
}
@media (min-width: 768px) {
  .gallery__img:before {
    width: 50px;
    height: 50px;
    right: 0px;
  }
}
.gallery__img:hover:after {
  border-left: 4px solid #30b6c5;
  border-bottom: 4px solid #30b6c5;
  transition: all 0.3s ease-in-out;
}
.gallery__img:hover:before {
  border-right: 4px solid #30b6c5;
  border-top: 4px solid #30b6c5;
  transition: all 0.3s ease-in-out;
}